/** @jsx jsx */
import { useEffect, useState } from 'react'
import { jsx } from "theme-ui";
import PropTypes from "prop-types";
import Layout from "@layout/index-5.jsx";
import SEO from "@components/seo";
import PageBreadcrumb from "@components/pagebreadcrumb";
import { graphql, useStaticQuery } from "gatsby";
import { Row, Container, Col } from "react-bootstrap";
import LargeSinglePosts from "../../components/large-single-post";
import LatestPostArea from '../../container/latest-post';
import { 
    BlogDetailsArea,
    BlogDetailsRightSidebar,
    Input,
    SearchContainer
} from "../../styles/style";
import { StaticImage } from "gatsby-plugin-image";
import { getQueryString } from '../../helpers/url'
import Parse from '../../vendor/b4a'

const CategorisPosts = ({ data, location, pageContext }) => {

    const [message, setMessage] = useState('')
    const  [resultList, setResultList] = useState([])
    const getResult = async (query) => {
        setMessage('Loading')
        const result = await Parse.Cloud.run('queryVideo_query', { query })

        setMessage('')
        setResultList(result)
    }
    const query = getQueryString('query')
    
    const [queryText, setQueryText] = useState(query)

    useEffect(() => {
        queryText && getResult(queryText)
    }, [])
    


    

    return (
        <Layout>
            <SEO title={"Blog Categories Post"} pathname="/" />
            <BlogDetailsArea>
                <Container>
                    <Row className="gx-5">
                    <Col lg={8} md={7}>
                    <Input 
                            placeholder="search"
                            value={queryText}
                            onChange={(e) => {
                                setQueryText(e.target.value)
                            }}
                        />
                    </Col>
                    <Col lg={4} md={5}>
                        <SearchContainer onClick={() => {
                            getResult(queryText)
                        }}>
                        <StaticImage src="../../../data/images/icons/search.png" alt=""/>
                        </SearchContainer>
                    </Col>
                    </Row>
                    <Row className="gx-5">

                    </Row>
                    <Row className="gx-5">
                        <Col lg={8} md={7}>
                            <Row>
                                {
                                    resultList && !resultList.length && !message && "No Result Found"
                                }
                                {
                                    message && `${message}`
                                }
                                {resultList && resultList.map((blog, i) => {
                                    return (
                                        <Col lg={12} key={i}>
                                            <LargeSinglePosts
                                                title={blog.node.title}
                                                thume_image={
                                                    blog.node.thumbnails.medium.url
                                                }
                                                categories={
                                                    ""
                                                }
                                                body={blog.node.excerpt}
                                                date={blog.node.date}
                                                slug={`listing/${blog.node.id}`}
                                                authorSlug={blog.node.i}
                                                authorId={blog.node.i}
                                                dateSlug={blog.node.i}
                                            />
                                        </Col>
                                    );
                                })}
                            </Row>
                        </Col>
                        <Col lg={4} md={5}>
                            {
                                "" && <BlogDetailsRightSidebar sx={{ mt: "-40px !important"}}>
                                
                                     <LatestPostArea/>
                                
                            </BlogDetailsRightSidebar>
                            }
                        </Col>
                    </Row>
                </Container>
            </BlogDetailsArea>
            
        </Layout>
    );
};

CategorisPosts.propTypes = {
    data: PropTypes.object,
    location: PropTypes.object,
    pageContext: PropTypes.object,
};




export default CategorisPosts;
